body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #EDF1F5;
}

*{
  margin: 0;
  padding: 0;
}


.bodyMain {
        display: flex;
       
}

.sidebar{
        box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.08);
        width: 240px;
        top: 0px;
        padding: 50px 10px 0px 10px;
        background: #ffffff;
        z-index: 99;
        height: 100%;
        position: fixed;
        top: 0;
        
}



.content{
        justify-content: center;
        width: 50%;
        
}

.cardEvent{
    background:#EDF1F5;
    position: relative;
    left: 50%;
    padding: 15px;
    display: block;
    overflow: hidden;
    margin: 80px 15px;
}

.img-circle{
        border-radius: 50%;
        width:90%;
}



.boxEvent{
            background: #ffffff;
            padding: 15px;
            display: block;
            overflow: hidden;
            margin-bottom: 15px;
}

.imgUser {
        border-radius: 50%;
        font-size: 30px;
        width: 80px;
        height: 90px;
        float: left;
}

.map{
        width: 100%;
        height: 300px;
}

.data-post h3{
        margin: 15px;
        
}

.link{
        text-decoration: none;
        color: black;
}

.formCreateEvent{
        width: 620px;
        margin-left: 20px;
}

.formCreateEvent input {
        margin: 10px;
        width: 300px;
        height: 30px;
        text-align: center;
        border-radius: 53px 54px 54px 54px;
        -moz-border-radius: 53px 54px 54px 54px;
        -webkit-border-radius: 53px 54px 54px 54px;
        border: 18px;
}

.row{
       margin-right: 15px;
       padding-left: 15px;
       margin-bottom: -8px;
}


.col-sm-2{
        width: 16.66666667%;
}

.col-xs-3 {
        width: 25%;
}
.modal-content i{
    text-align: right;
    font-weight: 10px;
}

.modal{
    margin: 100px;
    width: 300px;
    border-radius: 33px 33px 33px 33px;
    -moz-border-radius: 33px 33px 33px 33px;
    -webkit-border-radius: 33px 33px 33px 33px;
    border: 2px solid #000000;
    font-size: 20px;
   display: flex;
   background-color:white;
   position: absolute;

}

.modal-content{
    height: 100%;
    
}

.closeModal{
    display: flex;
    padding-top: 10px;
    padding-left: 10px;
    width: 50px;
}

.textModal{
    margin-top:-36px;
}
i {
    font-size: 30px;
}

.classNameNavBar {
    
    width: 100%;
    background-color: #4F5467;
    position: fixed;
    z-index: 98;
    top: 0;
}

.navBar {
    display: inline-flex;
    flex-wrap: nowrap;
    padding-right: 430px;
}

.navBarContent {
    display: flex;
    flex-direction: row;
    margin-left: 300px;

}

.navBarContent span {
    color: white;
    

}

.navBarContent i {
    margin: 10px;
    text-align: center;

}
